import axios from "axios";


export const getSearchData = (param) => {
  return axios({
    url: "https://restapi.amap.com/v3/place/text",
    params:{
      key:"3324f95ba5db3c0acc931d7edd729c5a",
      keywords:param.keywords,
      city:param.city??610100,
      extensions:param.extensions??"all",
      offset:param.offset??50,
      page:param.page??1
    }
  }).then((res) => {
    return res.data.pois;
  });
};
export const getData = () => {
  return axios({
    url: "https://docs.qq.com/sheet/DUldtQXlqbklRTnhs",
   
  }).then((res) => {
    return res;
  });
};