<template>
  <div class="item">
    <div
      class="title"
      style="display: flex; flex-direction: row; justify-content: space-between"
      @click="state.addShow = !state.addShow"
    >
      <span>添加shp</span>
    </div>
    <div v-show="state.addShow" class="content" style="padding: 15px;line-height: 36px;">
      <el-button
        id="ImportFileBut"
        class="btn btn-primary btn-sm glyphicon"
        style="border-radius: 3px"
        @click="startImport"
        >导入shp</el-button
      >
      <input
        id="ImportFileInp"
        type="file"
        name="file"
        multiple="multiple"
        accept=".shp,.dbf"
        style="display: none"
        @change="uploadFile"
      />
      <span style="line-height: 28px; color: #0091ea; font-size: 8px"
        >（请同时上传shp与dbf格式文件）</span
      >
      <br/>
      <span>坐标系</span>
      <el-select v-model="state.zbx"  size="small" style="width: 146px;margin-left: 17px;">
        <el-option label="4326" value="4326">4326 </el-option>
        <el-option label="3857" value="3857">3857</el-option>
        <el-option label="火星" value="火星">火星</el-option>
        <el-option label="百度" value="百度">百度</el-option>
      </el-select>
      <br/>
       <span>shp编码格式</span>
      <el-select v-model="state.shpbm"  size="small" style="width:102px;margin-left:17px">
        <el-option label="GBK" value="GBK">GBK </el-option>
        <el-option label="UTF-8" value="UTF-8">UTF-8</el-option>
      </el-select>
    </div>
  </div>
</template>
<script setup>
import { reactive, onMounted } from "vue";
import * as shapefile from "shapefile";
import { addLayerByFeatures } from "../../mapUtils/layerUtils";
const state = reactive({
  addShow: false,
  zbx: "4326",
  shpbm: "GBK"
});
const uploadFile = async (e) => {
  var features = [];
  let shpfile = null,
    dbffile = null;
  for (var i = 0; i < e.target.files.length; i++) {
    let file = e.target.files[i];
    if (file.name.indexOf(".shp") > -1) {
      shpfile = file;
    } else if (file.name.indexOf(".dbf") > -1) {
      dbffile = file;
    }
  }
  if (!shpfile) {
    return console.log("未检测到shp格式文件");
  }
  if (!dbffile) {
    return console.log("未检测到dbf格式文件");
  }
  let shp = await importFile(shpfile).then((res) => {
    return res;
  });
  let dbf = await importFile(dbffile).then((res) => {
    return res;
  });
  shapefile
    .open(shp, dbf, { encoding: state.shpbm })
    .then((source) => {
      source.read().then(function log(result) {
        if (result.done) {
          addLayerByFeatures(features, state.zbx);
        } else {
          features.push(result.value);
          return source.read().then(log);
        }
      });
    })
    .catch((error) => console.error(error.stack));
  e.target.value= null;  
};
const importFile = (file) => {
  return new Promise((resolve, reject) => {
    var reader = new FileReader();
    reader.readAsArrayBuffer(file);
    reader.onload = function (e) {
      resolve(e.target.result);
    };
  });
};
const startImport = () => {
  document.getElementById("ImportFileInp").click();
};
onMounted(() => {});
</script>

