
import { wgs84ToGd } from "./coordinateTransform"
export const getGeojsonByUrl = (features, zbx) => {
	let pFollect = {
		type: "FeatureCollection",
		features: [],
	};
	if (features.length) {
		//构建Geojson
		let pFeatures = [];
		for (let itm0 of features) {
			let itm=JSON.parse(JSON.stringify(itm0))
			if (zbx === "4326") {
				itm.geometry.coordinates = wgs84ToGd(itm.geometry.coordinates, itm.geometry.type);
			}
			if(itm.geometry.type.toLocaleLowerCase().indexOf("multi")>-1){
				itm.geometry.type=itm.geometry.type.substr(5)
				let coors=itm.geometry.coordinates
				coors.forEach(coor => {
					let itm2=JSON.parse(JSON.stringify(itm))
					itm2.geometry.coordinates=coor;
					pFeatures.push(itm2);
				});
			}else{
				pFeatures.push(itm);
			}
			
		}
		pFollect.features = pFeatures;
	}
	return pFollect;
};

export const getGeojsonByUrl2 = (list) => {
	const pFollect = {
		type: "FeatureCollection",
		features: [],
	};
	for (let item of list) {
		const coordinates = [Number(item.longitude), Number(item.latitude)]
		let geoObj = {
			type: 'Feature',
			properties: item,
			geometry: {
				coordinates: wgs84ToGd(coordinates, 'Point'),
				type: 'Point'
			}
		}
		pFollect.features.push(geoObj);
	}

	return pFollect
}
