<template>
  <div class="layer-div">
    <div class="item">
      <div class="title">底图</div>
      <div class="content">
        <div class="layerlist">
          <div
            :class="
              basemap.checked
                ? 'layerlist-item layerlist-item-focus'
                : 'layerlist-item'
            "
            v-for="(basemap, key) in state.basemaps"
            @click="basemapClick(basemap, event)"
            :key="key"
          >
            <img
              :class="basemap.checked ? 'img-focus' : 'img'"
              v-bind:src="basemap.icon"
              width="78"
              height="49"
            />
            <span class="layername">{{ basemap.name }}</span>
          </div>
        </div>
      </div>
    </div>

    <div class="item">
      <div
        class="title"
        style="
          display: flex;
          flex-direction: row;
          justify-content: space-between;
        "
        @click="toggleLayerListPanel(event)"
      >
        <span>图层</span>
        <span
          style="color: #61aefb; margin-right: 15px"
          @click="resetLayerClick()"
          >清除</span
        >
      </div>
      <div v-if="state.layerListShow" class="content">
        <div v-if="state.layers.length > 0" class="worklist">
          <div v-for="(layer, key) in state.layers" :key="key">
            <div class="worklist-item" v-if="layer.od == 1">
              <img
                v-if="layer.img"
                :src="layer.img"
                style="
                  width: 22px;
                  border-radius: 8px;
                  display: inline-block;
                  position: relative;
                  top: 7px;
                  margin-right: 4px;
                "
              />
              <span>{{ layer.title }}</span>
            </div>
            <div class="worklist-item" v-else>
              <img
                v-if="layer.img"
                :src="layer.img"
                style="
                  margin-left: 30px;
                  width: 22px;
                  border-radius: 8px;
                  display: inline-block;
                  position: relative;
                  top: 7px;
                  margin-right: 4px;
                "
              />
              <el-switch
                v-model="layer.checked"
                size="small"
                @change="layerItemClick(layer)"
              />
              <span>{{ layer.title }}</span>
            </div>
          </div>
        </div>
        <div
          v-if="state.layers.length == 0"
          style="margin-left: 16px; margin-top: 16px"
        >
          暂无图层
        </div>
      </div>
    </div>

    <xzqSearch ref="xzqse" />
    <importFile />
    <importGeojson/>
  </div>
</template>
<script setup>
import { reactive, onMounted, ref } from "vue";
import HxMap from "@/mapUtils/map";
import importFile from "./components/importFile.vue";
import importGeojson from "./components/importGeojson.vue";
import xzqSearch from "./components/xzqSearch.vue";
const xzqse = ref(null);
onMounted(() => {
 
});
const state = reactive({
  basemaps: [
    {
      name: "电子地图",
      checked: true,
      icon: require("@/assets/dzdt.png"),
      id: 0,
    },
    {
      name: "影像地图",
      checked: false,
      icon: require("@/assets/yxdt.png"),
      id: 1,
    },
  ],
  layers: [
    {
      img: require("@/assets/zyqy.png"),
      od: 1,
      title: "高德",
      name: "高德",
      checked: false,
    },
    {
      id: "gd_lk",
      img: require("@/assets/zyqy.png"),
      od: 2,
      title: "路况",
      name: "路况",
      checked: false,
      parentid: 0,
    },
    {
      id: "gd_lw",
      img: require("@/assets/zyqy.png"),
      od: 2,
      title: "路网",
      name: "路网",
      checked: false,
      parentid: 0,
    },
  ],
  layerListShow: false,
  selectlayer: {},
});
const toggleLayerListPanel = (e) => {
  state.layerListShow = !state.layerListShow;
};

const basemapClick = (basemap, e) => {
  state.basemaps[0].checked = false;
  state.basemaps[1].checked = false;
  //底图切换
  basemap.checked = true;
  document
    .querySelectorAll(".amap-ui-ctrl-layer-base-item input")
    [basemap.id].click();
};
const layerItemClick = (layer) => {
  if (layer.checked) {
    let player = null;
    if (layer.parentid == 0) {
      let ss = {
        gd_lk: new AMap.TileLayer.RoadNet(),
        gd_lw: new AMap.TileLayer.Traffic(),
      };
      player = ss[layer.id];
    } else {
      console.log("");
    }
    HxMap.mapView.addLayer(player);
    state.selectlayer[layer.id] = player;
  } else {
    if (state.selectlayer[layer.id]) {
      //   HxMap.mapView.removeLayer(state.selectlayer[layer.id]);
      state.selectlayer[layer.id].setMap(null);
      delete state.selectlayer[layer.id];
    }
  }
};
const resetLayerClick = () => {
  HxMap.loca.clear();
  HxMap.mapView.clearMap();
  let keys = Object.keys(state.selectlayer);
  keys.forEach((item) => {
    state.selectlayer[item].setMap(null);
    delete state.selectlayer[item];
  });
  state.layers.forEach((item) => {
    item.checked = false;
  });
  xzqse.value.setZg();
};
</script>
<style >
.layer-div {
  top: 0;
  width: 70%;
  height: 100%;
  position: absolute;
  right: 0;
  background-color: white;
  z-index: 999;
}

.item {
  width: 100%;
  height: auto;
}
.item .title,
.input-card .title {
  border-bottom: 1px solid #c9c2c2;
  font-family: "微软雅黑";
  font-size: 15px;
  color: #323232;
  padding-left: 16px;
  line-height: 36px;
  height: 36px;
  background: #eeeeee;
}
.item .content {
  width: 100%;
  height: auto;
}
.item .content .layerlist {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 16px 36px;
}
.item .content .layerlist-item {
  display: flex;
  flex-flow: column;
  align-items: center;
  font-family: "微软雅黑";
  font-size: 13px;
  color: #757575;
}
.item .content .layerlist-item .img {
  border: 1px solid #eeeeee;
}
.item .content .layerlist-item .img-focus {
  border: 1px solid #3296fa;
}
.item .content .layerlist-item-focus {
  color: #3296fa;
}
.item .content .worklist {
  width: 100%;
}
.item .content .worklist .worklist-item {
  /* display: flex; */
  flex-flow: row;
  align-items: center;
  justify-content: space-between;
  height: 36px;
  padding: 3px 20px;
  font-family: "微软雅黑";
  font-size: 13px;
  color: #000000;
  border-bottom: 1px solid #eeeeee;
}
.item .content .worklist .worklist-item .chockboxx {
  display: inline-block;
  position: fixed;
  right: 20px;
  margin-top: 8px;
}
.item .content .worklist .worklist-item input {
  border: 1px solid #ff0000;
}
</style>