<template>
  <MapView @changeShow="state.rightPanle = false"></MapView>

  <div class="tool-item-right">
    <img
      id="dtqh"
      width="44"
      height="44"
      src="@/assets/dtqh.png"
      @click="state.rightPanle = true"
    />

  </div>
  <poisearch />
  <layer v-show="state.rightPanle"></layer>
</template>
<script setup>
import MapView from "@/views/mapview/MapView.vue";
import poisearch from "./components/poisearch.vue";
import layer from "./layer.vue";
import { getData } from "../api/server";
import { reactive, onMounted } from "vue";
import axios from "axios";
import * as XLSX from "xlsx/xlsx.mjs";
const state = reactive({
  rightPanle: false,
});


const readWorkbook = (workbook) => {
  var sheetContent = [];
  var fromTo = '';

  // 遍历每张表读取
  for (var sheet in workbook.Sheets) {
    if (workbook.Sheets.hasOwnProperty(sheet)) {
     fromTo = workbook.Sheets[sheet]["!ref"];
      sheetContent.push(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
       break; // 如果只取第一张表，就取消注释这行
    }
  }
  return sheetContent;
};

onMounted(() => {});
</script>

<style >
.amap-control.amap-maptype {
  display: none;
}
.tool-item-right {
  position: absolute;
  top: 70px;
  right: 15px;
}
</style>
