<template>

   <div class="mapid" id="mapid"> </div>
</template>
<script>
import map from "@/mapUtils/map";
export default {
    async mounted () {
     let hxmap= await map.intMap('mapid') 
     var that=this;
     hxmap.on('click', function(e) {//地图点击事件
	  that.$emit('changeShow')

	});
      
    }
}
</script>
<style>
#mapid {
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0px;
  background-color: #deecf9;
}
.amap-logo{
    display: none!important;
    
}
.amap-copyright {
    opacity:0;
}
</style>