<template>
  <div class="input-card">
    <div
      class="title"
      style="display: flex; flex-direction: row; justify-content: space-between"
      @click="togglexzqPanel"
    >
      <span>行政区划查询</span>
    </div>
    <div v-show="state.xzqShow" class="input-card-content">
      <div class="input-item">
        <div class="input-item-prepend">
          <span class="input-item-text">省市区</span>
        </div>
        <select
          id="province"
          style="width: 140px; height: 30px"
          @change="search($event)"
        ></select>
      </div>
      <div class="input-item">
        <div class="input-item-prepend">
          <span class="input-item-text">地级市</span>
        </div>
        <select
          id="city"
          style="width: 140px; height: 30px"
          @change="search($event)"
        ></select>
      </div>
      <div class="input-item">
        <div class="input-item-prepend">
          <span class="input-item-text">区县</span>
        </div>
        <select
          id="district"
          style="width: 140px; height: 30px"
          @change="search($event)"
        ></select>
      </div>
      <div class="input-item">
        <div class="input-item-prepend">
          <span class="input-item-text">街道</span>
        </div>
        <select
          id="street"
          style="width: 140px; height: 30px"
          @change="setCenter($event)"
        ></select>
      </div>
    </div>
  </div>
</template>
<script setup>
import HxMap from "@/mapUtils/map";
import { reactive, onMounted } from "vue";
const state = reactive({
  xzqShow: false,
});
const togglexzqPanel = (e) => {
  state.xzqShow = !state.xzqShow;
};
var district;
const setZg = () => {
  //行政区划查询
  var opts = {
    subdistrict: 1, //返回下一级行政区
    showbiz: false, //最后一级返回街道信息
    extensions: "all",
  };
  district = new AMap.DistrictSearch(opts); //注意：需要使用插件同步下发功能才能这样直接使用
  district.search("中国", function (status, result) {
    if (status == "complete") {
      getData(result.districtList[0]);
    }
  });
};

onMounted(() => {
  setZg();
});
const getData = (data, level) => {
  var citySelect = document.getElementById("city");
  var districtSelect = document.getElementById("district");
  var areaSelect = document.getElementById("street");
  var bounds = data.boundaries;
  if (bounds) {
    let polygons = [];
    for (var i = 0, l = bounds.length; i < l; i++) {
      var polygon = new AMap.Polygon({
        // map: HxMap.mapView,
        strokeWeight: 1,
        strokeColor: "#0091ea",
        fillColor: "#80d8ff",
        fillOpacity: 0.2,
        path: bounds[i],
      });
      polygons.push(polygon);
    }
    var mask = [];
    for (var i = 0; i < bounds.length; i += 1) {
      mask.push([bounds[i]]);
    }
    HxMap.mapView.setMask(mask);
    HxMap.mapView.setFitView(polygons); //地图自适应
  }

  //清空下一级别的下拉列表
  if (level === "province") {
    citySelect.innerHTML = "";
    districtSelect.innerHTML = "";
    areaSelect.innerHTML = "";
  } else if (level === "city") {
    districtSelect.innerHTML = "";
    areaSelect.innerHTML = "";
  } else if (level === "district") {
    areaSelect.innerHTML = "";
  }

  var subList = data.districtList;
  if (subList) {
    var contentSub = new Option("--请选择--");
    var curlevel = subList[0].level;
    var curList = document.querySelector("#" + curlevel);
    curList.add(contentSub);
    for (var i = 0, l = subList.length; i < l; i++) {
      var name = subList[i].name;
      var levelSub = subList[i].level;
      var cityCode = subList[i].citycode;
      contentSub = new Option(name);
      contentSub.setAttribute("value", levelSub);
      contentSub.center = subList[i].center;
      contentSub.adcode = subList[i].adcode;
      curList.add(contentSub);
    }
  }
};
const search = (obj) => {
  //清除地图上所有覆盖物
  // for (var i = 0, l = polygons.length; i < l; i++) {
  //   polygons[i].setMap(null);
  // }
  var option = obj.target[obj.target.options.selectedIndex];
  var keyword = option.text; //关键字
  var adcode = option.adcode;
  district.setLevel(option.value); //行政区级别
  district.setExtensions("all");
  //行政区查询
  //按照adcode进行查询可以保证数据返回的唯一性
  district.search(adcode, function (status, result) {
    if (status === "complete") {
      getData(result.districtList[0], obj.target.id);
    }
  });
};
const setCenter = (obj) => {
  HxMap.mapView.setCenter(obj.target[obj.target.options.selectedIndex].center);
};
defineExpose({ setZg })
</script>

<style >
.input-card-content {
  width: 100%;
  padding: 5px 20px;
}
.input-item {
  height: 36px;
  line-height: 36px;
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.input-text {
  line-height: 2rem;
  margin-right: 2rem;
}

.info hr {
  margin-right: 0;
  margin-left: 0;
  border-top-color: grey;
}

.info {
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border-radius: 0.25rem;
  position: fixed;
  top: 1rem;
  background-color: white;
  width: auto;
  min-width: 22rem;
  border-width: 0;
  right: 1rem;
  box-shadow: 0 2px 6px 0 rgba(114, 124, 245, 0.5);
}

.code {
  left: 1.5rem;
  right: 1.5rem;
  top: 1.5rem;
  bottom: 1.5rem;
  overflow: auto;
  margin-bottom: 0rem;
}

.code .btn {
  top: 1rem;
  position: absolute;
  right: 1rem;
}

.code .result {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
  padding: 1rem;
  bottom: 1rem;
  position: absolute;
  top: 5.5rem;
  right: 1rem;
  left: 1rem;
  overflow: auto;
}

.code .status {
  color: #80adff;
  display: inline-block;
  font-size: 14px;
}

.code h4 {
  display: inline-block;
  max-width: 20rem;
  margin-right: 1rem;
  margin-bottom: 1rem;
}

select,
input[type="text"],
input[type="date"] {
  display: inline-block;
  width: 100%;
  padding: 0.375rem 1.75rem 0.375rem 0.75rem;
  line-height: 1.5;
  color: #495057;
  vertical-align: middle;
  background: #fff
    url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 4 5'%3E%3Cpath fill='%23343a40' d='M2 0L0 2h4zm0 5L0 3h4z'/%3E%3C/svg%3E")
    no-repeat right 0.75rem center;
  background-size: 8px 10px;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type="text"],
input[type="date"] {
  background: #fff;
  padding: 0.375rem 0.75rem;
}

select:focus,
input[type="text"]:focus,
input[type="date"]:focus {
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.1rem rgba(128, 189, 255, 0.1);
}

.btn:focus {
  outline: 0;
  box-shadow: none;
}

select:focus::-ms-value,
input[type="text"]:focus::-ms-value,
input[type="date"]:focus::-ms-value {
  color: #495057;
  background-color: #fff;
}
</style>
