<template>
  <div id="searchdiv">
    <div id="pickerBox">
      <el-input
        class="input-with-select"
        id="pickerInput"
        placeholder="地点查询"
        style="font-size: 14px; font-family: 'sans-serif'"
        v-model="state.searchvalue"
        @clear="stopSearch"
        @input="search"
        clearable
      >
        <template #prepend>
          <img
            src="@/assets/search.png"
            alt=""
            style="width: 22px; margin-top: 4px"
          />
        </template>
        <template #append>
          <span style="color: #15a2f4" @click="searchClick">搜索</span>
        </template>
      </el-input>
    </div>
    <div
      v-if="state.searching"
      class="weui-cells weui-cells_access search_show"
      
      style="
        margin-top: 0;
        display: block;
        max-height: 204px;
        overflow-y: auto;
        position: absolute;
        top: 0;
        top: 59px;
        left: 15px;
        right: 15px;
        padding: 10px 10px;
      "
    >
      <div
        class="weui-cell"
        v-for="(item, key) in state.searchItems"
        @click="searchItemClick(item)"
        :key="key"
      >
        <div class="weui-cell__hd" style="    line-height: 47px;">
          <img style="width: 18px; height: 24px" src="@/assets/position.png" />
        </div>
        <div
          class="weui-cell__bd"
          style="font-size: 14px; font-family: 'sans-serif'"
        >
          <p>{{ item.address }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script setup>
import HxMap from "@/mapUtils/map";
import { reactive, onMounted } from "vue";
import { getSearchData } from "../../api/server";
var marks=null,marker=null;
const state = reactive({
  searchItems: [],
  searching: false,
  canClear: false,
  searchvalue: "",
});
const search = async () => {
  var str = state.searchvalue;
  if (str && str.trim() != "") {
    let data = await getSearchData({ keywords: str });
    if (data && data.length > 0) {
      state.searching = true;
      state.searchItems = data;
    } else {
      state.searchItems = [];
    }
  }
};
const stopSearch = () => {
  //停止搜索
  if (state.searchvalue) {
    return;
  }
  state.searching = false;
  state.canClear = false;
  state.searchItems = [];
  state.searchvalue = "";
};
const searchStart = (e) => {
  state.searching = true;
  //$(".weui-cells").show();
  state.canClear = true;
};
const searchItemClick = (item) => {
  state.searching = false;
  state.searchvalue = item.address;
  state.canClear = true;
  if (marker != null) {
    HxMap.mapView.remove(marker);
  }
  marker = new AMap.Marker();
  var geo = item.location.split(",");
  var vvc = [Number(geo[0]), Number(geo[1])];
  marker.setExtData(item);
  marker.setMap(HxMap.mapView);
  marker.setPosition(vvc);
  HxMap.mapView.setZoomAndCenter(15, marker.getPosition());
};
const searchClick = () => {
  search();
};
const removeP = () => {
  if (marker != null) {
    map.remove(marker);
    marker = null;
  }

  if (selectMarker != null) {
    map.remove(selectMarker);
    selectMarker = null;
  }
};
const drawGeom = (data) => {
  data.foreach((item) => {
    if (item.geom) {
      var geo = item.location.split(",");
      var vcc = [Number(geo[0]), Number(geo[1])];
      var tempitem = JSON.parse(JSON.stringify(item));

      var type = getType(item);

      var circleMarker = new AMap.CircleMarker({
        //  map:map,
        center: vcc,
        radius: 8, //3D视图下，CircleMarker半径不要超过64px
        strokeColor: "white",
        strokeWeight: 1,
        fillColor: type,
        strokeOpacity: 1,
        fillOpacity: 1,
        extData: tempitem,
        zIndex: 990,
      });
      circleMarker.on("click", function (e) {});

      marks.push(circleMarker);
    }
  });
};
onMounted(() => {});
</script>

<style >
#pickerBox {
  position: absolute;
  /* z-index: 9999; */
  top: 15px;
  right: 15px;
  left: 15px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* z-index: 9999; */
}
.weui-cells.weui-cells_access.search_show {
  z-index: 9999;
}
.glyphicon-search.icon {
  width: 24px;
  height: 24px;
  margin: 8px 8px;
  line-height: 24px;
  color: #ccc;
  font-size: 18px;
}
#searchdiv {
  width: 100%;
  height: auto;
}
form {
  border: 0;
  width: 100%;
  height: auto;
}
#pickerInput {
  width: 100%;
  height: 42px;
  border: none;
  outline: none;
}
.close {
  flex-shrink: 0;
  border: 0;
  width: 36px;
  height: 32px;
  color: #666;
  text-align: right;
  padding-right: 8px;
  line-height: 32px;
  border-right: 1px solid #666 !important;
  margin-top: 6px;
  display: initial;
  float: none !important;
}
#poiInfo {
  background: #fff;
}
.search_show{
   background-color: white 
}
.weui-cell{
display: grid;
    grid-template-columns: 30px auto;
}

.weui-cell:hover{
    background-color: #e0e0e0;
}
</style>
