
import { initPro } from "./mapUtils";
export default class HxMap {
    static mapView;
    static loca;
    static async intMap (mapid) {
        
        var map = new AMap.Map(mapid, {
            viewMode: '3D',
           zoom:12,
           center: [108.997428, 34.30923],
           zooms:[0,20],
           resizeEnable: true,
           pitch:0,
           rotateEnable:true,
           pitchEnable:true,
           rotation: 0,
       });

       var loca =  new Loca.Container({
           map
       });
 
    var controlBar = new AMap.ControlBar({
        position:{
          right:'10px',
          bottom:'10px'
        }
      });
      controlBar.addTo(map);
      map.addControl(new AMap.MapType({
        defaultType:0 //0代表默认，1代表卫星
    }));
       this.mapView=map
       this.loca=loca
       return map;
    }
  }
  