

import HxMap from "./map";
import { getGeojsonByUrl } from "./GeojsonUtils";
export const addLayerByFeatures = (features, zbx) => {
    let pFollect = getGeojsonByUrl(features, zbx);
    createLayer(pFollect, features[0].geometry.type.toLocaleLowerCase())
}
const createLayer = (pFollect, type) => {
    let player = null;
    if (type.indexOf("point") > -1) {
        player = new Loca.IconLayer({
            loca: HxMap.loca,
            zooms: [6, 20],
            zIndex: 10,
            visible: true,
        })
        player.setSource(new Loca.GeoJSONSource({
            data: pFollect
        }))
        let imgs=[require('@/assets/layer/sx_fxy.png'),
        require('@/assets/layer/kshi.png'),
        require('@/assets/layer/now.png'),
        require('@/assets/layer/sx_wk.png'),
        require('@/assets/layer/sx_szqy.png'),
        require('@/assets/layer/sx_gyyq.png'),
        require('@/assets/layer/ksheng.png'),
        require('@/assets/layer/sx_sz.png'),
        require('@/assets/layer/zrbhq.png')

    ];
        // 设置样式
        player.setStyle({
            unit: 'px',
            icon: imgs[Math.floor(Math.random()*imgs.length)],
            iconSize: [10, 10],
            offset: [0, 0],
            rotation: 0,

        });
    } else if (type.indexOf("line") > -1) {
        player = new Loca.LineLayer({
            loca: HxMap.loca,
            zooms: [6, 20],
            zIndex: 10,
            visible: true,
            opacity: 1,
        });
        player.setSource(new Loca.GeoJSONSource({
            data: pFollect
        }))
        let colors=["blue","#f56c6c","#fbc4c4","#66b1ff","#85ce61","#e6a23c","#fde2e2"];
        player.setStyle({
            // 除了 color 属性，其他的缺省属性将恢复默认值。
            color: colors[Math.floor(Math.random()*colors.length)],
            lineWidth: 3
        });
    } else if (type.indexOf("polygon") > -1) {
        player = new AMap.GeoJSON({
            geoJSON: pFollect,
            getPolygon: function(geojson, lnglats) {
               
               
                let colors=["#f56c6c","#fbc4c4","#66b1ff","#85ce61","#e6a23c","#fde2e2"];
                return new AMap.Polygon({
                    path: lnglats,
                    fillOpacity: 0.5,// 面积越大透明度越高
                    strokeColor: "#14f5fd",
                    fillColor: colors[Math.floor(Math.random()*colors.length)]
                });
            }
        });
        HxMap.mapView.add(player)
    }


}