<template>
  <div class="item">
    <div
      class="title"
      style="display: flex; flex-direction: row; justify-content: space-between"
      @click="state.addShow = !state.addShow"
    >
      <span>添加geojson</span>
    </div>
    <div
      v-show="state.addShow"
      class="content"
      style="padding: 15px; line-height: 36px"
    >
      <el-button
        id="ImportFileBut"
        class="btn btn-primary btn-sm glyphicon"
        style="border-radius: 3px"
        @click="startImport"
        >导入geojson</el-button
      >
      <input
        id="ImportFilejson"
        type="file"
        name="file"
        multiple="multiple"
        accept=".json,.geojson"
        style="display: none"
        @change="uploadFile"
      />

      <br />
      <span>坐标系</span>
      <el-select
        v-model="state.zbx"
        size="small"
        style="width: 146px; margin-left: 17px"
      >
        <el-option label="4326" value="4326">4326 </el-option>
        <el-option label="3857" value="3857">3857</el-option>
        <el-option label="火星" value="火星">火星</el-option>
        <el-option label="百度" value="百度">百度</el-option>
      </el-select>
    </div>
  </div>
</template>
<script setup>
import { reactive, onMounted } from "vue";
import { addLayerByFeatures } from "../../mapUtils/layerUtils";
const state = reactive({
  addShow: false,
  zbx: "4326",
});
const uploadFile = async (e) => {
  
  let file = e.target.files[0];
  // 格式判断
  if (file.type !== "application/json") {
    reject("仅允许上传json文件");
  }
  // 检验是否支持FileRender
  if (typeof FileReader === "undefined") {
    reject("当前浏览器不支持FileReader");
  }

  let f = await importFile(file).then((res) => {
    return res;
  });
  addLayerByFeatures(f.features, state.zbx);
  e.target.value = null;
};
const importFile = (file) => {
  return new Promise((resolve, reject) => {
    var reader = new FileReader();
    reader.readAsText(file);
    reader.onload = function (e) {
      resolve(JSON.parse(e.target.result));
    };
  });
};
const startImport = () => {
  document.getElementById("ImportFilejson").click();
};
onMounted(() => {});
</script>

